	},
	onLoad(option) {
		this.globalData.initType = 2
		uni.setNavigationBarTitle({
			title: option.name, // 对方的名称
		});
		this.fromUserId = option.fromUserId; // 当前登录的用户 id
		this.toUserId = option.toUserId; // 对方的用户 id
		this.fromAvatarPath = uni.getStorageSync('fromAvatarPath') || option.fromAvatarPath || 'https://ossyidap.oss-cn-shenzhen.aliyuncs.com/image/png/9EAFE4BFEFDDF762718332C8F1BE9F2C.png'; // 当前登录用户的头像
		this.toAvatarPath = uni.getStorageSync('toAvatarPath') || option.toAvatarPath || 'https://ossyidap.oss-cn-shenzhen.aliyuncs.com/image/png/9EAFE4BFEFDDF762718332C8F1BE9F2C.png'; // 对方的用户头像
		this.userInfoId =  parseInt(this.fromUserId) > parseInt(this.toUserId) ? md5.md5(this.toUserId + this.fromUserId) : md5.md5(this.fromUserId + this.toUserId);
		if (this.globalData.connentSocket) { // 已连接则直接获取聊天历史
			let message = {
				fromUserId: this.fromUserId,
				toUserId: this.toUserId,
				content: 'page',
				smsType: 'TEXT',
				sysType: 1,
				smsStatus: 10,
				smsList: false,
				currentPage: 1,
				pageSize: 15
			}
			this.globalData.localSocket.send({
				data: JSON.stringify(message)
			})
		} else { // 未连接则连接过后再获取聊天历史
			socket.initSocket(this.fromUserId, this.toUserId)
		}
		//语音自然播放结束
		this.AUDIO.onEnded((res)=>{
			this.playMsgid=null;
		});

		//录音开始事件
		this.RECORDER.onStart((e)=>{
			this.recordBegin(e);
		})
		//录音结束事件
		this.RECORDER.onStop((e)=>{
			this.recordEnd(e);
		})

	},
	onShow() {
		let that = this;
		this.globalData.callback = function (res) {
			if (String(res.userInfoId) == '0' || res.fromUserId == 0) {
				return;
			}
			if (res && res.list) {
				that.nextPage = res.totalPage != that.currentPage;
				let result = res.list;
				if (result.length > 0) {
					for (let i = 0; i < result.length; i++) {
						if (result[i].smsType == 'IMAGE') {
							result[i] = that.setPicSize(result[i])
						}
					}
					that.msgList = result.concat(that.msgList)
					
					that.$nextTick(function () {
						if (that.currentPage == 1) {
							// 滚动到底部
							that.scrollTop = 99999;
						} else {
							if (that.nextPage) {
								uni.createSelectorQuery()
									.select('#msg' + (15 + that.msgTotal))
									.boundingClientRect()
									.exec(res => {
										if (res[0] != null) {
											that.scrollTop = res[0].top;
										}
									})
							}
							that.toupperTop = true
						}
					})
				}
			} else {
				if (res.userInfoId == that.userInfoId) {
					let index = that.msgList.push(res);
					that.toView(index)
				} else {
					that.$emit('chatList', true)
				}
			}
		}
	},
	methods: {
		toupper(){
			let that = this;
			if (that.nextPage && that.toupperTop) {
				that.toupperTop = false;
				that.currentPage = that.currentPage + 1
				let message = {
					fromUserId: that.fromUserId,
					toUserId: that.toUserId,
					content: 'page',
					smsType: 'TEXT',
					sysType: 1,
					smsStatus: 10,
					smsList: false,
					currentPage: that.currentPage,
					pageSize: 15
				}
				that.globalData.localSocket.send({
					data: JSON.stringify(message)
				})
			}
		},
		//处理图片尺寸，如果不处理宽高，新进入页面加载图片时候会闪
		setPicSize(row){
			let maxW = uni.upx2px(350);
			let maxH = uni.upx2px(350);
			if(row.width > maxW || row.height > maxH) {
				let scale = row.width / row.height;
				if(row.width > row.height) {
					row.width = maxW;
					row.height = row.width / scale
				} else {
					row.height = maxH;
					row.width = row.height * scale;
				}
			}
			return row;
		},
		// 选择表情
		chooseEmoji(){
			this.showEmji = this.showEmji=='showEmoji'?'hideEmoji':'showEmoji';
		},
		// 隐藏表情
		hideEmoji(){
			this.showEmji = this.showEmji=='showEmoji'?'hideEmoji':'';
		},
		//添加表情
		addEmoji(em){
			this.textMsg+=em.alt;
		},
		// 预览图片
		showPic(row){
			var src = row.content; //获取data-src
			let index = src.indexOf('?')
			if (index != -1) {
				src = src.substring(0, index) + '?x-oss-process=style/big'
			}
			uni.previewImage({
				indicator: "none",
				current: src,
				urls: [src]
			});
		},
		// 播放语音
		playVoice(msg){
			this.playMsgid=msg.id;
			this.AUDIO.src = msg.msg.url;
			console.log("msg.msg.url: " + msg.msg.url);
		    this.AUDIO.play();
		},
		sendText(e){
			this.hideEmoji();
			let that = this;
			let content = e.detail.value ? e.detail.value : that.textMsg
			if (!content || content == '') {
				return
			}
			let messageId = new Date().getTime() + that.fromUserId + that.toUserId + math.math(5);
			messageId = md5.md5(messageId);
			
			let message = {
				fromUserId: that.fromUserId,
				toUserId: that.toUserId,
				messageId: messageId,
				content: content,
				smsType: 'TEXT',
				sysType: 1,
				smsStatus: 10,
				smsList: false,
				currentPage: '',
				pageSize: ''
			}
			
			this.globalData.localSocket.send({
				data: JSON.stringify(message),
				success: () => {
					let index = that.msgList.push(message);
					that.toView(index);
					that.textMsg = '';
					that.msgTotal = that.msgTotal + 1;
					message.dateTime = that.getTime();
					uni.$emit('message', message)
				},
				fail: () => {
					console.log('发送失败！！！')
				}
			})
		},
		// 选择图片发送
		chooseImage(){
			this.hideEmoji();
			let that = this;
			uni.chooseImage({
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				success: (res)=>{
					uni.showLoading({
						title: '发送中',
						mask: false
					});
					let tempFilePaths = res.tempFilePaths;
					for (let index in tempFilePaths) {
						uni.getImageInfo({
							src: tempFilePaths[index],
							success: (image)=>{
								let messageId = new Date().getTime() + that.fromUserId + that.toUserId + math.math(5);
								messageId = md5.md5(messageId);
								let message = {
									fromUserId: that.fromUserId,
									toUserId: that.toUserId,
									messageId: messageId,
									content: image.path,
									smsType: 'IMAGE',
									sysType: 1,
									smsStatus: 10,
									smsList: false,
									currentPage: '',
									pageSize: '',
									width: image.width,
									height: image.height
								}
								that.upload(message)
							}
						});
					}
				}
			});
		},
		upload(message) {
			let that = this;
			let data = {};
			const access_token = wx.getStorageSync('access_token') || '';
			data.file = '[object Object]';
			data.type = 'big';
			let timestamp = Date.parse(new Date());
			data.timestamp = timestamp;
			data.sign = util.makeSign(api.apiUrl + '/api/upload', data);
			data.deviceId = "wx";
			data.platformType = "2";
			data.versionCode = '4.0';
			let uploadTask = uni.uploadFile({
				url: `${api.apiUrl}/api/upload`,
				filePath: message.content,
				name: 'file',
				header: {
					'content-type': 'multipart/form-data',
					'Accept': 'application/json',
					'Authorization': `Bearer ${access_token}`
				},
				formData: data,
				success: (res) => {
					var result = JSON.parse(res.data);
					console.log('result', result)
					if (result.code == 200 || result.code == 0 ) {
						message.content = result.data;
						that.globalData.localSocket.send({
							data: JSON.stringify(message),
							success: () => {
								message = that.setPicSize(message)
								let index = that.msgList.push(message);
								that.toView(index);
								that.msgTotal = that.msgTotal + 1;
								message.dateTime = that.getTime();
								that.$emit('message', message)
							}
						})
					}
				},
				complete: () => {
					uni.hideLoading()
				}
			})
		},
		toView(index) {
			let that = this;
			this.$nextTick(function(){
				that.scrollToView = 'msg' + (index - 1)
			})
		},
		getTime() {
			let date = new Date();
			return date.getHours() + ':' + date.getMinutes();
		},
		sendBlur(event) {






		},
		discard(){
			return;
		}
	}
}
